<template>
  <div>
    <el-card class="box-card">
      <div slot="header" class="clearfix header-box">
        <span>文章分类</span>
        <el-button @click="addVisible = true" type="primary" size="mini"
          >添加分类</el-button
        >
      </div>
      <!-- 使用 el-table 渲染数据 -->
      <!-- border: 带边框 -->
      <!-- stripe: 斑马纹 -->
      <el-table stripe border :data="cateList" style="width: 100%">
        <!-- 表格的序号: 将 table-column 的 type 设置为 index, 自动生成序号 -->
        <el-table-column type="index" label="序号"> </el-table-column>
        <el-table-column prop="cate_name" label="分类名称"> </el-table-column>
        <el-table-column prop="cate_alias" label="分类别名"> </el-table-column>
        <el-table-column label="操作">
          <template v-slot="scope">
            <el-button @click="editOpen(scope.row)" size="mini" type="primary"
              >修改</el-button
            >
            <el-button
              @click="delHandler(scope.row.id)"
              size="mini"
              type="danger"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <el-dialog
      @closed="addCloseHandler"
      title="添加文章分类"
      :visible.sync="addVisible"
      width="25%"
    >
      <span>
        <el-form
          :model="addForm"
          :rules="addRules"
          ref="addForm"
          label-width="70px"
          class="demo-ruleForm"
        >
          <el-form-item label="分类名称" prop="cate_name">
            <el-input v-model="addForm.cate_name"></el-input>
          </el-form-item>
          <el-form-item label="分类别名" prop="cate_alias">
            <el-input v-model="addForm.cate_alias"></el-input>
          </el-form-item>
        </el-form>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="addVisible = false">取 消</el-button>
        <el-button size="mini" type="primary" @click="addHandler"
          >添加</el-button
        >
      </span>
    </el-dialog>
    <!-- 修改文章 -->
    <el-dialog
      @closed="addHandlerzc"
      title="修改文章分类"
      :visible.sync="editVisible"
      width="25%"
    >
      <span>
        <el-form
          :model="editForm"
          :rules="addRules"
          ref="editForm"
          label-width="70px"
          class="demo-ruleForm"
        >
          <el-form-item label="活动名称" prop="cate_name">
            <el-input v-model="editForm.cate_name"></el-input>
          </el-form-item>
          <el-form-item label="活动名称" prop="cate_alias">
            <el-input v-model="editForm.cate_alias"></el-input>
          </el-form-item>
        </el-form>
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button size="mini" @click="editVisible = false">取 消</el-button>
        <el-button size="mini" type="primary" @click="editHandler"
          >添加</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'ArtCate',
  created () {
    // 发请求获取文章分类数据
    this.getArtCate()
  },
  data () {
    return {
      addForm: {
        cate_name: '',
        cate_alias: ''
      },
      editForm: {
        cate_name: '',
        cate_alias: ''
      },
      cateList: [],
      addVisible: false,
      editVisible: false,
      addRules: {
        cate_name: [
          { required: true, message: '请输入名称', trigger: 'blur' },
          {
            pattern: /^\S{1,10}$/,
            message: '分类名必须式1-10位的非空字符',
            trigger: 'blur'
          }
        ],
        cate_alias: [
          { required: true, message: '请输入名称', trigger: 'blur' },
          {
            pattern: /^[a-z-zA-Z0-9]{1,15}$/,
            message: '分类名必须式1-15的字母数字',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  methods: {
    async getArtCate () {
      const { data: res } = await this.$http.get('/my/cate/list')
      if (res.code === 0) {
        this.cateList = res.data
      }
    },
    addCloseHandler () {
      // 点击删除表单的内容
      // resetFields() 清除表单的方法
      this.$refs.addForm.resetFields()
    },
    addHandlerzc () {
      this.$refs.editForm.resetFields()
    },
    // 添加分类
    addHandler () {
      this.$refs.addForm.validate(async (result) => {
        if (!result) return
        const { data: res } = await this.$http.post(
          '/my/cate/add',
          this.addForm
        )
        if (res.code === 0) {
          this.addVisible = false
          this.getArtCate()
        }
      })
    },
    editHandler () {
      this.$refs.editForm.validate(async (result) => {
        if (!result) return
        const { data: res } = await this.$http.put(
          '/my/cate/info',
          this.editForm
        )
        if (res.code === 0) {
          this.editVisible = false
          this.getArtCate()
        }
      })
    },
    editOpen (row) {
      if (row.id === 1 || row.id === 2) { return this.$message.error('管理员不允许修改') }
      this.editVisible = true
      this.editForm = { ...row }
    },
    async delHandler (id) {
      if (id === 1 || id === 2) return this.$message.error('不准删!!')
      //   console.log(id)
      // this.$confirm会返回一个promise对象 如果点击确认会执行.then的第一个回调函数表示成功
      //   this.$confirm('确认删除吗宝').then(res => {
      //     console.log(res)
      //   }).catch(err => {
      //     console.log(err)
      //   })
      const res = await this.$confirm('确认删除吗宝').catch((e) => e)
      //   console.log(res)
      if (res === 'confirm') {
        //   第二个参数直接写数据对象表示传入{data} 请求体
        const { data: res } = await this.$http.delete('/my/cate/del', {
          params: { id }
        })
        if (res.code === 0) this.getArtCate()
      }
    }
  }
}
</script>

<style lang="less" scoped>
.header-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>
